
.loginLogo {
  width: 25vw;
  min-width: 150px;
  height: 15%;
  text-align: center;
  top: 5%;
  position: absolute;
}

.loginbox {
  background-color: var(--primary);
  height: 350px;
  width: 50vw;
  min-width: 500px;
  max-width: 700px;
  position: absolute;
  border-radius: 6px;
  min-height: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.loginContent {
  padding-left: 30px;
  padding-right: 30px;
}
.registerContent {
  padding-left: 30px;
  padding-right: 30px; 
}
.textbox {
  text-align: left;
  position: relative;
}
.form-group {
  margin-top: 40px;
  height: 30px;
  width: 45.5%;
  border-radius: 4px;
  border: none;
}
.loginTextBox {
  background-color: var(--primary-dark);
  width: 45.5%;
  height: 240px;
  border-radius: 4px;
  float: right;
  color: var(--white);
  font-size: 1rem;
  text-align: center;
}
@media (max-width: 1000px) {
  div.loginTextBox {
    display: none;
  }
  .form-group {
    width: 99%;
  }
}
.form-group-register {
  margin-top: 40px;
  height: 30px;
  border-radius: 4px;
  border: none;
  width: 45.5%;
}
.checkboxText {
  font-size: 1rem;
}
#regemail {
  margin-left: 30px;
  float: right;
}
#regconfirm_password {
  margin-left: 30px;
  float: right;
}
.loginButton {
   bottom: 0;
   left: 0vw;
   width: 25vw;
   height: 50px;
   min-height: 50x;
   min-width: 250px;
   max-width: 350px;
   position: absolute;
   border: none;
   background: var(--primary-dark);
   color: var(--white);
   border-bottom-left-radius: 6px;
   text-decoration: none;
   list-style: none;
   font-size: medium;
   text-align: center;
   line-height: 50px;
}
.registerButton {
   bottom: 0;
   right: 0;
   width: 25vw;
   height: 50px;
   min-height: 50px;
   min-width: 250px;
   max-width: 350px;
   position: absolute;
   border: none;
   background: var(--primary-dark);
   color: var(--white);
   border-bottom-right-radius: 6px;
   text-decoration: none;
   list-style: none;
   font-size: medium;
   text-align: center;
   line-height: 50px;
}
.forgotPass {
  text-decoration: none;
  list-style: none;
  color: var(--white);
  font-size: medium;
  cursor: pointer;
}
.registerButton:hover {
  background: var(--secondary);
}
.loginButton:hover {
  background: var(--secondary);
}
.logoWhite {
  display: none;
}


@media (max-width: 500px) {
  .loginbox {
  top:3vh;
  left:3vw;
  width: 94vw;
  height: 94vh;
  bottom: 3vh;
  border-radius: 6px;
  min-height: 470px;
  max-height: none;
  min-width: 0;
  box-shadow: none;
  }
  .logoWhite {
  display: flex;
  position: absolute;
  width: 33.3%;
  left: 33.3%;


  }
  .loginButton {
   bottom: 0;
   width: 47vw;
   height: 10vh;
   min-height: 65px;
   min-width: 0;
