   border: none;
   background: var(--primary-dark);
   color: var(--white);
   border-bottom-left-radius: 6px;
   text-decoration: none;
   list-style: none;
   font-size: medium;
   text-align: center;
   line-height: 10vh;
}
.registerButton {
   bottom: 0;
   left: 47vw;
   width: 47vw;
   height: 10vh;
   min-height: 65px;
   min-width: 0;
   position: absolute;
   border: none;
   background: var(--primary-dark);
   color: var(--white);;
   border-bottom-right-radius: 6px;
   text-decoration: none;
   list-style: none;
   font-size: medium;
   text-align: center;
   line-height: 10vh;
}
.form-group {
  margin-top: 30px;
  height: 30px;
  width: 99%;
  min-width: 76vw;
  bottom: 5px;
  border-radius: 4px;
  border: none;
  position: relative;
  display: flex;
}
.space {
  display: none;
}
.form-group-register {
  margin-top: 30px;
  height: 30px;
  width: 80vw;
  min-width: 76vw;
  border-radius: 4px;
  border: none;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}
#regemail {
  margin-left: 0;
  float: none;
}
#regconfirm_password {
  margin-left: 0;
  float: none;
}
.loginContent {
  margin-top: 16vh;
  padding: 25px;
}
.registerContent {
  margin-top: 16vh;
  padding: 25px;
}
.loginLogo{
  visibility: hidden;
}}
@media (max-height: 650px) {
  .loginLogo{
    visibility: hidden;
  }
}